import React, { useState, useEffect } from "react";
import { useJobStore } from "../store/useJobStore";
import {
  Form,
  Input,
  Button,
  Card,
  Typography,
  Row,
  Col,
  Space,
  Collapse,
  Radio,
  Select,
  DatePicker,
  message,
  Pagination,
} from "antd";
import { PlusOutlined, DeleteOutlined, EditOutlined } from "@ant-design/icons";
import dayjs from "dayjs";
import request from "../utils/request";

const { TextArea } = Input;
const { Title } = Typography;
const { Option } = Select;

const Job = () => {
  const { jobs, loading, fetchJobs, addJob, updateJob, deleteJob } =
    useJobStore();
  const [form] = Form.useForm();
  const [activeView, setActiveView] = useState<"publish" | "list">("publish");
  const [editingJobId, setEditingJobId] = useState<string | null>(null);
  const [departments, setDepartments] = useState<any[]>([]);
  const [departmentsLoading, setDepartmentsLoading] = useState<boolean>(false);
  const [searchText, setSearchText] = useState<string>("");
  const [filterStatus, setFilterStatus] = useState<string>("all");
  const [filterDepartment, setFilterDepartment] = useState<string>("all");
  const [currentPage, setCurrentPage] = useState<number>(1);
  const pageSize = 5;

  useEffect(() => {
    fetchJobs();
    fetchDepartments();
  }, [fetchJobs]);

  const fetchDepartments = async () => {
    setDepartmentsLoading(true);
    try {
      const response = await request.get("/departments");
      setDepartments(response.data.data);
    } catch (error) {
      console.error(":", error);
    } finally {
      setDepartmentsLoading(false);
    }
  };

  const handleSubmit = async (values: any) => {
    try {
      if (editingJobId) {
        await updateJob(editingJobId, {
          ...values,
          expiryDate: values.expiryDate
            ? values.expiryDate.format("YYYY-MM-DD")
            : undefined,
        });
        message.success("");
        setEditingJobId(null);
      } else {
        await addJob({
          ...values,
          expiryDate: values.expiryDate
            ? values.expiryDate.format("YYYY-MM-DD")
            : undefined,
        });
        message.success("");
      }
      form.resetFields();
    } catch (error) {
      console.error(":", error);
    }
  };

  const handleEdit = (job: any) => {
    setActiveView("publish");
    setEditingJobId(job._id);

    // 
    const departmentValue =
      typeof job.department === "object" ? job.department._id : job.department;

    form.setFieldsValue({
      ...job,
      department: departmentValue,
      expiryDate: job.expiryDate ? dayjs(job.expiryDate) : undefined,
    });
  };

  const handleDelete = async (id: string) => {
    try {
      await deleteJob(id);
      message.success("");
    } catch (error) {
      console.error(":", error);
    }
  };

  const handleCancel = () => {
    form.resetFields();
    setEditingJobId(null);
  };

  const getDepartmentName = (job: any) => {
    if (typeof job.department === "object" && job.department) {
      return job.department.name;
    }
    return job.department || "";
  };

  // 
  const filteredJobs = jobs.filter((job) => {
    // 
    const matchesSearch = searchText
      ? job.title.toLowerCase().includes(searchText.toLowerCase()) ||
        job.description.toLowerCase().includes(searchText.toLowerCase()) ||
        job.location.toLowerCase().includes(searchText.toLowerCase())
      : true;

    // 
    const matchesStatus =
      filterStatus === "all" ? true : job.status === filterStatus;

    // 
    const jobDeptId =
      typeof job.department === "object" && job.department
        ? job.department._id
        : job.department;
    const matchesDepartment =
      filterDepartment === "all" ? true : jobDeptId === filterDepartment;

    return matchesSearch && matchesStatus && matchesDepartment;
  });

  // 
  const paginatedJobs = filteredJobs.slice(
    (currentPage - 1) * pageSize,
    currentPage * pageSize
  );

  // 
  const resetFilters = () => {
    setSearchText("");
    setFilterStatus("all");
    setFilterDepartment("all");
    setCurrentPage(1);
  };

  return (
    <Row justify="center" style={{ padding: 24 }}>
      <Col span={16}>
        <Radio.Group
          value={activeView}
          onChange={(e) => setActiveView(e.target.value)}
          style={{ marginBottom: 24 }}
          buttonStyle="solid"
        >
          <Radio.Button value="publish"></Radio.Button>
          <Radio.Button value="list"></Radio.Button>
        </Radio.Group>

        {activeView === "publish" ? (
          <Collapse defaultActiveKey={["1"]}>
            <Collapse.Panel
              header={editingJobId ? "" : ""}
              key="1"
            >
              <Card style={{ marginBottom: 24 }}>
                <Form form={form} layout="vertical" onFinish={handleSubmit}>
                  <Form.Item
                    label=""
                    name="title"
                    rules={[{ required: true, message: "" }]}
                  >
                    <Input placeholder="" />
                  </Form.Item>

                  <Form.Item
                    label=""
                    name="department"
                    rules={[{ required: true, message: "" }]}
                  >
                    <Select
                      placeholder=""
                      loading={departmentsLoading}
                    >
                      {departments.map((dept) => (
                        <Option key={dept._id} value={dept._id}>
                          {dept.name}
                        </Option>
                      ))}
                    </Select>
                  </Form.Item>

                  <Form.Item
                    label=""
                    name="description"
                    rules={[{ required: true, message: "" }]}
                  >
                    <TextArea rows={4} placeholder="" />
                  </Form.Item>

                  <Form.Item
                    label=""
                    name="location"
                    rules={[{ required: true, message: "" }]}
                  >
                    <Input placeholder="" />
                  </Form.Item>

                  <Form.Item label="" name="salaryRange">
                    <Input placeholder="15k-25k" />
                  </Form.Item>

                  <Form.Item
                    label=""
                    name="status"
                    rules={[{ required: true, message: "" }]}
                    initialValue="open"
                  >
                    <Select placeholder="">
                      <Option value="open"></Option>
                      <Option value="closed"></Option>
                      <Option value="archived"></Option>
                    </Select>
                  </Form.Item>

                  <Form.Item label="" name="jobType">
                    <Select placeholder="">
                      <Option value=""></Option>
                      <Option value=""></Option>
                      <Option value=""></Option>
                    </Select>
                  </Form.Item>

                  <Form.Item label="" name="expiryDate">
                    <DatePicker
                      placeholder=""
                      style={{ width: "100%" }}
                    />
                  </Form.Item>

                  <Form.Item>
                    <Space>
                      <Button
                        type="primary"
                        htmlType="submit"
                        icon={
                          editingJobId ? <EditOutlined /> : <PlusOutlined />
                        }
                        loading={loading}
                      >
                        {editingJobId ? "" : ""}
                      </Button>
                      {editingJobId && (
                        <Button onClick={handleCancel}></Button>
                      )}
                    </Space>
                  </Form.Item>
                </Form>
              </Card>
            </Collapse.Panel>
          </Collapse>
        ) : (
          <>
            <Title level={3}></Title>

            {/*  */}
            <Card style={{ marginBottom: 16 }}>
              <Row gutter={[16, 16]}>
                <Col span={8}>
                  <Input.Search
                    placeholder=""
                    value={searchText}
                    onChange={(e) => setSearchText(e.target.value)}
                    onSearch={(value) => setSearchText(value)}
                    allowClear
                  />
                </Col>
                <Col span={6}>
                  <Select
                    placeholder=""
                    style={{ width: "100%" }}
                    value={filterStatus}
                    onChange={(value) => setFilterStatus(value)}
                  >
                    <Option value="all"></Option>
                    <Option value="open"></Option>
                    <Option value="closed"></Option>
                    <Option value="archived"></Option>
                  </Select>
                </Col>
                <Col span={6}>
                  <Select
                    placeholder=""
                    style={{ width: "100%" }}
                    value={filterDepartment}
                    onChange={(value) => setFilterDepartment(value)}
                    loading={departmentsLoading}
                  >
                    <Option value="all"></Option>
                    {departments.map((dept) => (
                      <Option key={dept._id} value={dept._id}>
                        {dept.name}
                      </Option>
                    ))}
                  </Select>
                </Col>
                <Col span={4}>
                  <Button onClick={resetFilters}></Button>
                </Col>
              </Row>
            </Card>

            {loading ? (
              <div>...</div>
            ) : (
              <>
                <Space direction="vertical" style={{ width: "100%" }}>
                  {filteredJobs.length === 0 ? (
                    <Card>
                      <Typography.Text></Typography.Text>
                    </Card>
                  ) : (
                    paginatedJobs.map((job) => (
                      <Card
                        key={job._id}
                        actions={[
                          <Button
                            key="edit"
                            type="link"
                            icon={<EditOutlined />}
                            onClick={() => handleEdit(job)}
                          >
                            
                          </Button>,
                          <Button
                            key="delete"
                            type="link"
                            danger
                            icon={<DeleteOutlined />}
                            onClick={() => handleDelete(job._id)}
                          >
                            
                          </Button>,
                        ]}
                      >
                        <Title level={4} style={{ margin: 0 }}>
                          {job.title}
                          <Typography.Text
                            type={
                              job.status === "open"
                                ? "success"
                                : job.status === "closed"
                                ? "warning"
                                : "secondary"
                            }
                            style={{ fontSize: "14px", marginLeft: "10px" }}
                          >
                            {job.status === "open"
                              ? ""
                              : job.status === "closed"
                              ? ""
                              : ""}
                          </Typography.Text>
                        </Title>
                        <Typography.Text type="secondary">
                          {getDepartmentName(job)}  {job.location}
                          {job.jobType && `  ${job.jobType}`}
                        </Typography.Text>
                        {job.salaryRange && (
                          <Typography.Text
                            type="success"
                            style={{ display: "block", marginTop: 8 }}
                          >
                            {job.salaryRange}
                          </Typography.Text>
                        )}
                        <Typography.Paragraph style={{ marginTop: 16 }}>
                          <strong></strong>
                          <br />
                          {job.description}
                        </Typography.Paragraph>
                        {job.expiryDate && (
                          <Typography.Text type="secondary">
                            :{" "}
                            {dayjs(job.expiryDate).format("YYYY-MM-DD")}
                          </Typography.Text>
                        )}
                      </Card>
                    ))
                  )}
                </Space>

                {/*  */}
                {filteredJobs.length > 0 && (
                  <Row justify="end" style={{ marginTop: 16 }}>
                    <Col>
                      <Typography.Text style={{ marginRight: 16 }}>
                         {filteredJobs.length} 
                      </Typography.Text>
                      <Pagination
                        current={currentPage}
                        pageSize={pageSize}
                        total={filteredJobs.length}
                        onChange={(page) => setCurrentPage(page)}
                        showSizeChanger={false}
                      />
                    </Col>
                  </Row>
                )}
              </>
            )}
          </>
        )}
      </Col>
    </Row>
  );
};

export default Job;
